<template>
  <div
    id="icon"
    class="ml-4 mt-4"
  >
    <img
      alt="Pokemon Icon"
      :src="getImg(pkm)"
      width="192px"
    >
  </div>
</template>

<script>

export default {
  name: 'Icon',
  data() {
    return {
    }
  },
  computed: {
    pkm() {
      return String(this.$store.state.pkm_calc.id).padStart(4, '0') + "_" + String(this.$store.state.pkm_calc.form).padStart(3, '0')
    }

  },
  methods: {
    clear() {
      this.$router.push(this.$route.path)
    },
    getImg(pkm) {
      var images = require.context('@/assets/img/pokemon/big/normal/', false, /\.png$/)
      return images('./' + pkm + ".png")
    }
  }
}
</script>

