<template>
  <div id="InputEvAtk">
    <v-container
      id="input-usage"
      fluid
    >
      <span>
        &nbsp;&nbsp;{{ subheader }}
      </span>
      <v-text-field
        v-model.number="$store.state.pkm_calc.ev_atk"
        class=""
        type="number"
        :rules="rules"
        outlined
        :placeholder="placeholder"
        solo
        dense
        background-color="orange accent-1"
        color="red darken-1"
        :label="placeholder"
        @keyup="$store.commit('calc');queryData()"
        @click="$store.commit('calc');queryData()"
      />
    </v-container>
  </div>
</template>

<script>

export default {
  name: 'InputEvAtk',
  data() {
    return {
      subheader: this.$t('subheader_stat_atk'),
      placeholder: this.$t('placeholder_ev_atk'),
      rules: [
        value => ( value >= 0 && value <= 255)
      ],
    }

  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { ea: this.$store.state.pkm_calc.ev_atk }) })
    }
  },
}
</script>

