<script>
import { Radar, mixins } from 'vue-chartjs'
const { reactiveProp } = mixins
import ChartDataLabels from 'chartjs-plugin-datalabels';

export default {
  name: 'ChartBase',
  extends: Radar,
  mixins: [reactiveProp],
  props: {
    chartData: {
      type: Object,
      default: null
    },
    options: {
      type: Object,
      default: null
    }
  },
  mounted() {
    this.addPlugin(ChartDataLabels);
    this.renderChart(this.chartData, this.options)
  },
}
</script>