<template>
  <div id="InputStatSpeed">
    <v-container
      id="input-usage"
      fluid
      class="mt-n8 mt-sm-0"
    >
      <span>
        &nbsp;&nbsp;{{ subheader }}
      </span>
      <v-text-field
        id=""
        v-model.number="$store.state.pkm_calc.stat_speed"
        class=""
        type="number"
        outlined
        :placeholder="placeholder"
        solo
        dense
        flat
        background-color="pink accent-1"
        color="red darken-1"
        :label="placeholder"
        @keyup="$store.commit('calc');queryData()"
        @click="$store.commit('calc');queryData()"
      />
    </v-container>
  </div>
</template>

<script>
export default {
  name: 'InputStatSpeed',
  data() {
    return {
      subheader: this.$t('subheader_stat_speed'),
      placeholder: this.$t('placeholder_stat_speed')
    }

  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { s: this.$store.state.pkm_calc.stat_speed }) })
    }
  },
}
</script>

