<script>
import Base_Stats from './components/base.vue'
import Ev from './components/Ev.vue'
import Judge from './components/Judge.vue'
import Level from './components/Level.vue'
import Stats from './components/Stats.vue'
import ad from './components/ad/ad.vue'
import characteristic from './components/characteristic.vue'
import game from './components/game.vue'
import hidden_power from './components/hidden_power.vue'
import nature from './components/nature.vue'
import outputs from './components/output.vue'
import pokemon from './components/pokemon.vue'

