  name: 'Calc',
  components: {
    Base_Stats,
    ad,
    Ev,
    Judge,
    Level,
    Stats,
    characteristic,
    game, 
    hidden_power,
    nature,
    outputs,
    pokemon
  },
  mounted () {
    this.fillData()
  },
  methods: {
    fillData() {

      if (!isNaN(Number(this.$route.query.id)) && !isNaN(Number(this.$route.query.form))){ 
        this.$store.state.pkm_calc.id = Number(this.$route.query.id)
        this.$store.state.pkm_calc.form = Number(this.$route.query.form)
        this.$store.state.pkm_calc.value = Number(String(this.$store.state.pkm_calc.id) + String(this.$store.state.pkm_calc.form))
        this.$store.commit('setIcon')
        this.$store.commit('setBase')}
      
      if (!isNaN(Number(this.$route.query.g))){
        this.$store.state.pkm_calc.game = Number(this.$route.query.g)}

      if (!isNaN(Number(this.$route.query.n))){
        this.$store.state.pkm_calc.nature = Number(this.$route.query.n)}
      
      if (!isNaN(Number(this.$route.query.t)))
      {this.$store.state.pkm_calc.hidden_power = Number(this.$route.query.t)}

      if (!isNaN(Number(this.$route.query.c)))
      {this.$store.state.pkm_calc.characteristic = Number(this.$route.query.c)}

      if (!isNaN(Number(this.$route.query.jo))){
        this.$store.state.pkm_calc.judge_overview = Number(this.$route.query.jo)}

      if (!isNaN(Number(this.$route.query.jh))){
        this.$store.state.pkm_calc.judge_hp = Number(this.$route.query.jh)}

      if (!isNaN(Number(this.$route.query.ja))){
