      if (!isNaN(Number(this.$route.query.jsa))){
        this.$store.state.pkm_calc.judge_satk = Number(this.$route.query.jsa)}

      if (!isNaN(Number(this.$route.query.jsd))){
        this.$store.state.pkm_calc.judge_sdef = Number(this.$route.query.jsd)}

      if (!isNaN(Number(this.$route.query.js))){
        this.$store.state.pkm_calc.judge_speed = Number(this.$route.query.js)}

      if (!isNaN(Number(this.$route.query.l))){
        this.$store.state.pkm_calc.level = Number(this.$route.query.l)}

