        this.$store.state.pkm_calc.ev_def = Number(this.$route.query.ed)}

      if (!isNaN(Number(this.$route.query.esa))){
        this.$store.state.pkm_calc.ev_satk = Number(this.$route.query.esa)}

      if (!isNaN(Number(this.$route.query.esd))){
        this.$store.state.pkm_calc.ev_sdef = Number(this.$route.query.esd)}

      if (!isNaN(Number(this.$route.query.es))){
        this.$store.state.pkm_calc.ev_speed = Number(this.$route.query.es)}

      this.$store.commit('calc')
    }
