<template>
  <div id="Base">
    <v-hover
      disabled
    >
      <v-tooltip bottom>
        <template v-slot:activator="{on, attrs}">
          <v-card
            class="ma-2 pa-2 px-4 py-4"
            raised
            min-height=450
            :elevation="hover ? 12 : 8"
            :class="{'on-hover': hover}"
            rounded
            color="grey lighten-5"
            v-bind="attrs"
            v-on="on"
            @mouseover="hover = true"
            @mouseleave="hover = false"
          >
            <div class="justify-center">
              <v-spacer>
                <ChartBase
                  :height="auto"
                  :chart-data="datacollection"
                  :options="options"
                />
                <v-spacer />
              </v-spacer>
            </div>
          </v-card>
        </template>
        <span> {{ tooltip }} </span>
      </v-tooltip>
    </v-hover>
  </div>
</template>

<script>
import ChartBase from './output/ChartOutput.vue'

const config = {
  legend: {
    display: false,
  },
  layout: {
    margin: 30
  },
  tooltips: {
    enabled: false
  },
  scale: {
    ticks: {
      display: false,
      maxTicksLimit: 6,
      min: 0,
      max: 31
    },
    gridLines: {
      display: true,
    },
    angleLines: {
      display: true
    },

    
  },
  
  responsive: false,
  maintainAspectRatio: false,
  plugins: {
    datalabels: {
      display: false,
      align: 'end',
      anchor: 'end',
      clamp: true,
      font: {
        size: 18,
        weight: "bold"
      }
    }
  }
}

export default {
  name: '',
  components: {
    ChartBase
  },
  data () {
    return {
      error: this.$t('error_msg'),
      hover: false,
      tooltip: this.$t('tooltip_output'),
      datacollection: null,
      options: config,
      label_hp: this.$t('subheader_stat_hp'),
      label_atk: this.$t('subheader_stat_atk'),
      label_def: this.$t('subheader_stat_def'),
      label_satk: this.$t('subheader_stat_satk'),
      label_sdef: this.$t('subheader_stat_sdef'),
      label_speed: this.$t('subheader_stat_speed')
    }
  },
  computed: {
    change () {
      
      var i = this.$store.state.pkm_calc.iv_hp_max + this.$store.state.pkm_calc.iv_atk_max + this.$store.state.pkm_calc.iv_def_max
      i = i + this.$store.state.pkm_calc.iv_satk_max + this.$store.state.pkm_calc.iv_sdef_max + this.$store.state.pkm_calc.iv_speed_max
      i = i + this.$store.state.pkm_calc.iv_hp_min + this.$store.state.pkm_calc.iv_atk_min + this.$store.state.pkm_calc.iv_def_min
      if(this.$store.state.pkm_calc.iv_hp_max === null) {i++}
      if(this.$store.state.pkm_calc.iv_atk_max === null) {i++}
      if(this.$store.state.pkm_calc.iv_def_max === null) {i++}
      if(this.$store.state.pkm_calc.iv_satk_max === null) {i++}
      if(this.$store.state.pkm_calc.iv_sdef_max === null) {i++}
      if(this.$store.state.pkm_calc.iv_speed_max === null) {i++}
      return i + this.$store.state.pkm_calc.iv_satk_min + this.$store.state.pkm_calc.iv_sdef_min + this.$store.state.pkm_calc.iv_speed_min
    },
    
  },
  watch: {
    chartData: {
      deep: true,
      handler() {
        this.updateChart++
      }
    },
    change () {
      this.fillData()
    }
    
  },
  mounted () {
    this.fillData()
  },
  methods: {
    setLabel() {
      var hp
      var atk
      var def
      var satk
      var sdef
      var speed

      if ( this.$store.state.pkm_calc.iv_hp_max === null || this.$store.state.pkm_calc.iv_hp_min === null )
      { hp = this.error }
      else if (this.$store.state.pkm_calc.iv_hp_max === this.$store.state.pkm_calc.iv_hp_min ) 
      { hp = this.$store.state.pkm_calc.iv_hp_max}
      else 
      { hp = this.$store.state.pkm_calc.iv_hp_min + " - " + this.$store.state.pkm_calc.iv_hp_max}

      if ( this.$store.state.pkm_calc.iv_atk_max === null || this.$store.state.pkm_calc.iv_atk_min === null )
      { atk = this.error }
      else if (this.$store.state.pkm_calc.iv_atk_max === this.$store.state.pkm_calc.iv_atk_min ) 
      { atk = this.$store.state.pkm_calc.iv_atk_max}
      else {atk = this.$store.state.pkm_calc.iv_atk_min + " - " + this.$store.state.pkm_calc.iv_atk_max}

      if ( this.$store.state.pkm_calc.iv_def_max === null || this.$store.state.pkm_calc.iv_def_min === null )
      { def = this.error }
      else if (this.$store.state.pkm_calc.iv_def_max === this.$store.state.pkm_calc.iv_def_min) 
      { def = this.$store.state.pkm_calc.iv_def_max}
      else {def = this.$store.state.pkm_calc.iv_def_min + " - " + this.$store.state.pkm_calc.iv_def_max}

      if ( this.$store.state.pkm_calc.iv_satk_max === null || this.$store.state.pkm_calc.iv_satk_min === null )
      { satk = this.error }
      else if (this.$store.state.pkm_calc.iv_satk_max === this.$store.state.pkm_calc.iv_satk_min) 
      { satk = this.$store.state.pkm_calc.iv_satk_max}
      else {satk = this.$store.state.pkm_calc.iv_satk_min + " - " + this.$store.state.pkm_calc.iv_satk_max}

      if ( this.$store.state.pkm_calc.iv_sdef_max === null || this.$store.state.pkm_calc.iv_sdef_min === null )
      { sdef = this.error }
      else if (this.$store.state.pkm_calc.iv_sdef_max === this.$store.state.pkm_calc.iv_sdef_min) 
      { sdef = this.$store.state.pkm_calc.iv_sdef_max}
      else {sdef = this.$store.state.pkm_calc.iv_sdef_min + " - " + this.$store.state.pkm_calc.iv_sdef_max}
 
      if ( this.$store.state.pkm_calc.iv_speed_max === null || this.$store.state.pkm_calc.iv_speed_min === null)
      { speed = this.error }
      else if (this.$store.state.pkm_calc.iv_speed_max === this.$store.state.pkm_calc.iv_speed_min) 
      { speed = this.$store.state.pkm_calc.iv_speed_max}
      else { speed = this.$store.state.pkm_calc.iv_speed_min + " - " + this.$store.state.pkm_calc.iv_speed_max}

      this.label_hp = [this.$t('subheader_stat_hp'), hp]
      this.label_atk = [this.$t('subheader_stat_atk'), atk]
      this.label_def = [this.$t('subheader_stat_def'), def]
      this.label_satk = [this.$t('subheader_stat_satk'), satk]
      this.label_sdef = [this.$t('subheader_stat_sdef'), sdef]
      this.label_speed = [this.$t('subheader_stat_speed'), speed]
    },

    fillData() {
      this.setLabel()
      this.datacollection = {
        
        labels: [this.label_hp, this.label_atk, this.label_def, this.label_speed, this.label_sdef, this.label_satk],
        datasets: [
          {
            backgroundColor: [
              'rgba(130, 177, 255, 0.5)',
            ],
            data: [this.$store.state.pkm_calc.iv_hp_max, this.$store.state.pkm_calc.iv_atk_max, this.$store.state.pkm_calc.iv_def_max, 
                   this.$store.state.pkm_calc.iv_speed_max, this.$store.state.pkm_calc.iv_sdef_max, this.$store.state.pkm_calc.iv_satk_max]
          },
          {
            backgroundColor: [
              'rgba(130, 177, 255, 1)'
            ],
            data: [this.$store.state.pkm_calc.iv_hp_min, this.$store.state.pkm_calc.iv_atk_min, this.$store.state.pkm_calc.iv_def_min, 
                   this.$store.state.pkm_calc.iv_speed_min, this.$store.state.pkm_calc.iv_sdef_min, this.$store.state.pkm_calc.iv_satk_min]
          },
          
        ],
        
      };
    },
  },
  
}
</script>

