<template>
  <div id="InputStatHp">
    <v-container
      id="input-usage"
      fluid
    >
      <span>
        &nbsp;&nbsp;{{ subheader }}
      </span>
      <v-text-field
        id=""
        v-model.number="$store.state.pkm_calc.stat_hp"
        class=""
        type="number"
        outlined
        :placeholder="placeholder"
        solo
        dense
        flat
        background-color="red accent-1"
        color="red darken-1"
        :label="placeholder"
        @click="$store.commit('calc');queryData()"
        @keyup="$store.commit('calc');queryData()"
      />
    </v-container>
  </div>
</template>

<script>
export default {
  name: 'InputStatHp',
  data() {
    return {
      subheader: this.$t('subheader_stat_hp'),
      placeholder: this.$t('placeholder_stat_hp'),
    }

  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { h: this.$store.state.pkm_calc.stat_hp }) })
    }
  },
}
</script>

