<template>
  <div class="text-center">
    <v-menu offset-y>
      <template v-slot:activator="{on, attrs}">
        <v-btn
          slot="activator"
          color="blue"
          dark
          v-bind="attrs"
          v-on="on"
        >
          <v-icon left>
            mdi-google-translate
          </v-icon>
          <span>{{ lable }}</span>
        </v-btn>
      </template>
      <v-list>
        <v-list-item
          v-for="lang in langs"
          :key="lang.key"
          @click="$i18n.locale = lang.key"
          @change="changeLocale"
        >
          <v-list-item-title>{{ lang.titel }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </div>
</template>


<script>
export default {
  name: 'LanguageSwitcher',
  data() {
    return { 
      lable  : this.$t('lable_locale_changer'),
      langs: [  {key: 'de',  titel: "Deutsch"}, 
                {key: 'en',  titel: "English"}, 
                {key: 'es',  titel: "Español"}, 
                {key: 'fr',  titel: "Français"}, 
                {key: 'it',  titel: "Italiano"}, 
                {key: 'ja',  titel: "日本"},
                {key: 'ko',  titel: "한국인"},
                {key: 'ru',  titel: "Pусский"},
                {key: 'tw',  titel: "台湾"},
                {key: 'zh',  titel: "中国人"},
      
      ],
      path: '',
    };
  },
  methods: {
    changeLocale() {
      this.$router.push({path: ['', this.$i18n.locale, this.$route.name].join('/'), query: this.$route.query})
      this.$router.go(0)
    }
  }
};
</script>

