<template>
  <div id="characteristic">
    <v-hover
      disabled
    >
      <v-tooltip bottom>
        <template v-slot:activator="{on, attrs}">
          <v-card
            class="ma-2 pa-2"
            raised
            :elevation="hover ? 12 : 8"
            :class="{'on-hover': hover}"
            rounded
            color="grey lighten-5"
            v-bind="attrs"
            v-on="on"
            @mouseover="hover = true"
            @mouseleave="hover = false"
          >
            <v-card-titel>
              <heading_characteristic />
            </v-card-titel>
            <v-layout row>
              <v-flex
                warp
                xs12
                md12
              >
                <select_characteristic />
              </v-flex>
            </v-layout>
          </v-card>
        </template>
        <span> {{ tooltip }} </span>
      </v-tooltip>
    </v-hover>
  </div>
</template>

<script>

import heading_characteristic from './characteristic/heading_characteristic.vue'
import select_characteristic from './characteristic/select_characteristic.vue'

export default {
  name: 'Characteristic',
  components: {
    heading_characteristic,
    select_characteristic, 
  },
  data() {
    return {
      hover: false,
      tooltip: this.$t('tooltip_characteristic'),
    }
  }

}

</script>

