<template>
  <div id="select_game">
    <v-container fluid>
      <v-autocomplete
        v-model.number="$store.state.pkm_calc.game"
        class="mb-n7"
        item-text="titel"
        item-value="key"
        :no-data-text="noData"
        :items="items"
        :label="label"
        auto-select-first
        hide-selected
        solo
        background-color="grey lighten-5"
        @change="$store.commit('calc');$store.commit('setBase');queryData()"
        @keyup="$store.commit('calc');$store.commit('setBase');queryData()"
        @click="$store.commit('calc');$store.commit('setBase');queryData()"
      />
    </v-container>
  </div>
</template>

<script>

export default {
  name: '',
  data() {
    return {
      label: this.$t('label_game'),
      noData: this.$t('no_data'),
      items:[ {key: null , titel: ''},
              {key: 0, titel : this.$t('game_0')},
              {key: 1, titel : this.$t('game_1')},
              {key: 2, titel : this.$t('game_2')},
              {key: 3, titel : this.$t('game_3')},
              {key: 4, titel : this.$t('game_4')},
              {key: 5, titel : this.$t('game_5')},
              {key: 6, titel : this.$t('game_6')},
              {key: 7, titel : this.$t('game_7')},
              {key: 8, titel : this.$t('game_8')},
              {key: 9, titel : this.$t('game_9')}]
    }
  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { g: this.$store.state.pkm_calc.game }) })
    }
  },
}
</script>

