<template>
  <div id="characteristic">
    <v-hover
      disabled
    >
      <v-tooltip bottom>
        <template v-slot:activator="{on, attrs}">
          <v-card
            class="ma-2 pa-1"
            raised
            :elevation="hover ? 12 : 8"
            :class="{'on-hover': hover}"
            rounded
            color="grey lighten-5"
            v-bind="attrs"
            v-on="on"
            @mouseover="hover = true"
            @mouseleave="hover = false"
          >
            <v-card-titel>
              <HeaderLevel />
            </v-card-titel>
            <v-layout row>
              <v-flex
                warp
                xs12
                md12
              >
                <InputLevel />
              </v-flex>
            </v-layout>
          </v-card>
        </template>
        <span> {{ tooltip }} </span>
      </v-tooltip>
    </v-hover>
  </div>
</template>

<script>

import HeaderLevel from './level/HeaderLevel.vue'
import InputLevel from './level/InputLevel.vue'

export default {
  name: 'Characteristic',
  components: {
    HeaderLevel,
    InputLevel
  },
  data() {
    return {
      hover: false,
      tooltip: this.$t('tooltip_characteristic'),
    }
  }

}

</script>

