<template>
  <div id="hidden_power">
    <v-hover
      disabled
    >
      <v-tooltip bottom>
        <template v-slot:activator="{on, attrs}">
          <v-card
            class="ma-2 pa-2"
            raised
            :elevation="hover ? 12 : 8"
            :class="{'on-hover': hover}"
            rounded
            color="grey lighten-5"
            v-bind="attrs"
            v-on="on"
            @mouseover="hover = true"
            @mouseleave="hover = false"
          >
            <v-card-titel>
              <heading_hidden_power />
            </v-card-titel>
            <v-layout row>
              <v-flex
                warp
                xs12
                md12
              >
                <select_hidden_power />
              </v-flex>
            </v-layout>
          </v-card>
        </template>
        <span> {{ tooltip }} </span>
      </v-tooltip>
    </v-hover>
  </div>
</template>

<script>

import heading_hidden_power from './hidden_power/heading_hidden_power.vue'
import select_hidden_power from './hidden_power/select_hidden_power.vue'

export default {
  name: 'HiddenPower',
  components: {
    heading_hidden_power,
    select_hidden_power
  },
  data() {
    return {
      hover: false,
      tooltip: this.$t('tooltip_hidden_power'),
    }
  }
}

</script>

