  mounted () {
    this.init()
  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { id: this.$store.state.pkm_calc.id, form: this.$store.state.pkm_calc.form }) })
    },
    init() {
      if (!isNaN(Number(this.$route.query.id)) && !isNaN(Number(this.$route.query.form))){ 
        console.log("Test ", Number(String(this.$route.query.id) + String(this.$route.query.form)))
