<template>
  <div id="InputLevel">
    <v-container
      id="input-usage"
      fluid
    >
      <span>
        &nbsp;&nbsp;{{ label }}
      </span>
      <v-text-field
        v-model.number="$store.state.pkm_calc.level"
        class="mb-n5 mx-1"
        type="number"
        outlined
        :placeholder="placeholder"
        solo
        dense
        flat
        background-color="grey accent-1"
        color="red darken-1"
        :label="placeholder"
        @click="$store.commit('calc');queryData()"
        @keyup="$store.commit('calc');queryData()"
      />
    </v-container>
  </div>
</template>

<script>
export default {
  name: 'InputLevel',
  data() {
    return {
      label: this.$t('label_level'),
      placeholder: this.$t('placeholder_level')
    }

  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { l: this.$store.state.pkm_calc.level }) })
    }
  },
}
</script>

