// src/router.js

import Vue from "vue";
import Router from "vue-router";
import calc from '../calc/calc.vue'
import blacklist from '../blacklist/blacklist.vue'
import imprint from '../imprint/imprint.vue'
import discord from './extern/Discord.js'
import twitter from './extern/Twitter.js'
import instagram from './extern/Instagram.js'

import { i18n } from "../main.js";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/imprint",
      name: "imprint",
      component: imprint,
      
    },
    {
      path: "/discord",
      name: "Discord",
      component: discord,
    },
    {
      path: "/twitter",
      name: "Twitter",
      component: twitter,
    },
    {
      path: "/instagram",
      name: "Instagram",
      component: instagram,
    },
    {
      path: "/impressum",
      name: "impressum",
      component: imprint,
      
    },
    {
      path: "/:lang",
      component: calc,
      beforeEnter(to, from, next) {
        const lang = to.params.lang;
        if (!['en', 'de', 'es', 'fr', 'it', 'ja', 'ko', 'ru', 'tw', 'zh'].includes(lang)) return next('en');

        if (i18n.locale !== lang) {
          i18n.locale = lang;
        }
        return next();
      },
      children: [
        {
          path: "calc",
          name: "calc",
          component: calc
        },
      ]
    },
    {
      path: "/:lang",
      component: blacklist,
      beforeEnter(to, from, next) {
        const lang = to.params.lang;
        if (!['en', 'de', 'es', 'fr', 'it', 'ja', 'ko', 'ru', 'tw', 'zh'].includes(lang)) return next('en');

        if (i18n.locale !== lang) {
          i18n.locale = lang;
        }
        return next();
      },
      children: [
        {
          path: "blacklist",
          name: "blacklist",
          component: blacklist
        },
      ]
    },
    {
      path: "*",
      redirect: "/en/calc",
      
    },
  ]
});