<template>
  <div id="nature">
    <v-hover
      disabled
    >
      <v-tooltip bottom>
        <template v-slot:activator="{on, attrs}">
          <v-card
            class="ma-2 pa-2"
            raised
            :elevation="hover ? 12 : 8"
            :class="{'on-hover': hover}"
            rounded
            
            color="grey lighten-5"
            v-bind="attrs"
            :disabled="$store.state.disabled.nature"
            v-on="on"
            @mouseover="hover = true"
            @mouseleave="hover = false"
          >
            <v-card-titel>
              <heading_nature />
            </v-card-titel>
            <v-layout row>
              <v-flex
                warp
                xs12
                md12
              >
                <select_nature />
              </v-flex>
            </v-layout>
          </v-card>
        </template>
        <span> {{ tooltip }} </span>
      </v-tooltip>
    </v-hover>
  </div>
</template>

<script>

import heading_nature from './nature/heading_nature.vue'
import select_nature from './nature/select_nature.vue'

export default {
  name: 'Nature',
  components: {
    heading_nature,
    select_nature
  },
  data() {
    return {
      hover: false,
      tooltip: this.$t('tooltip_nature'),
    }
  }
}

</script>

