<template>
  <div id="SelectPotential">
    <v-container fluid>
      <span>
        &nbsp;&nbsp;&nbsp;{{ label }}
      </span>
      <v-select
        v-model.number="$store.state.pkm_calc.judge_overview"
        class="mb-n8 mx-1"
        item-text="titel"
        item-value="key"
        :no-data-text="noData"
        :placeholder="placeholder"
        :items="items"
        label="label"
        auto-select-first
        hide-selected
        solo
        background-color="grey lighten-5"
        @change="$store.commit('calc');queryData()"
        @keyup="$store.commit('calc');queryData()"
        @click="$store.commit('calc');queryData()"
      />
    </v-container>
  </div>
</template>

<script>
export default {
  name: 'SelectPotential',

  data() {
    return {
      activ: true,
      label  : this.$t('label_judge_overview'),
      placeholder  : this.$t('label_judge_overview'),
      noData: this.$t('no_data'),
      items: [  
        { key: null, titel: ""},
        { key: 1, titel: this.$t('potential_1')},
        { key: 2, titel: this.$t('potential_2')},
        { key: 3, titel: this.$t('potential_3')},
        { key: 4, titel: this.$t('potential_4')} ]
    }
  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { jo: this.$store.state.pkm_calc.judge_overview }) })
    }
  },

}
</script>

