<template>
  <div id="SelectHiddenPower">
    <v-container fluid>
      <v-autocomplete
        v-model.number="$store.state.pkm_calc.hidden_power"
        class="mb-n7"
        item-text="titel"
        item-value="key"
        :no-data-text="noData"
        :items="items"
        :label="label"
        auto-select-first
        hide-selected
        solo
        background-color="grey lighten-5"
        @change="$store.commit('calc');queryData()"
        @keyup="$store.commit('calc');queryData()"
        @click="$store.commit('calc');queryData()"
      />
    </v-container>
  </div>
</template>

<script>

export default {
  name: 'SelectHiddenPower',
  
  data() {
    return {
      label: this.$t('label_hidden_power'),
      noData: this.$t('no_data'),
      items:[ { key: null, titel: ''},
              { key: 0, titel: this.$t('hidden_power_0')},
              { key: 1, titel: this.$t('hidden_power_1')},
              { key: 2, titel: this.$t('hidden_power_2')},
              { key: 3, titel: this.$t('hidden_power_3')},
              { key: 4, titel: this.$t('hidden_power_4')},
              { key: 5, titel: this.$t('hidden_power_5')},
              { key: 6, titel: this.$t('hidden_power_6')},
              { key: 7, titel: this.$t('hidden_power_7')},
              { key: 8, titel: this.$t('hidden_power_8')},
              { key: 9, titel: this.$t('hidden_power_9')},
              { key: 10, titel: this.$t('hidden_power_10')},
              { key: 11, titel: this.$t('hidden_power_11')},
              { key: 12, titel: this.$t('hidden_power_12')},
              { key: 13, titel: this.$t('hidden_power_13')},
              { key: 14, titel: this.$t('hidden_power_14')},
              { key: 15, titel: this.$t('hidden_power_15')},
              { key: 16, titel: this.$t('hidden_power_16')},
              { key: 17, titel: this.$t('hidden_power_17')} ]
    }
  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { t: this.$store.state.pkm_calc.hidden_power }) })
    }
  },
}
</script>

