<template>
  <div id="Base">
    <v-hover
      disabled
    >
      <v-tooltip bottom>
        <template v-slot:activator="{on, attrs}">
          <v-card
            class="justify-center ma-2 pa-6"
            raised
            :elevation="hover ? 12 : 8"
            :class="{'on-hover': hover}"
            rounded
            min-height=336
            color="grey lighten-5"
            v-bind="attrs"
            v-on="on"
            @mouseover="hover = true"
            @mouseleave="hover = false"
          >
            <v-card-titel />
            <v-layout row>
              <v-flex
                warp
                xs12
                md12
              >
                <ChartBase
                  :height=304
                  :chart-data="datacollection"
                  :options="options"
                />
              </v-flex>
            </v-layout>
          </v-card>
        </template>
        <span> {{ tooltip }} </span>
      </v-tooltip>
    </v-hover>
  </div>
</template>

<script>
import ChartBase from './base/ChartBase.vue'

const config = {
  legend: {
    display: false,
  },
  gridLines: {
    display: false
  },
  tooltips: {
    enabled: false
  },
  scales: {
    xAxes: [{
      gridLines: {
        display: false
      },   
      ticks: {
        min: 0,
        //max: 180,
        stepSize: 10,
        maxTicksLimit: 10,
        display: false
      }
    }],
    yAxes: [{
      barThickness : 36,
      gridLines: {
        drawBorder: false,
        display: false
      },   
      ticks: {
        display: true,
        fontSize: 10
      }
    }]
  },
  
  responsive: false,
  maintainAspectRatio: false,
  plugins: {
    datalabels: {
      display: true,
      align: 'start',
      anchor: 'end',
      clamp: true,
      font: {
        size: 16,
        weight: "bold"
      }
    }
  }
}

export default {
  name: '',
  components: {
    ChartBase
  },
  data () {
    return {
      tooltip: this.$t('tooltip_base'),
      hover: false,
      datacollection: null,
      options: config
    }
  },
  computed: {
    change () {
      return this.$store.state.pkm_calc.id + this.$store.state.pkm_calc.form + this.$store.state.pkm_calc.game
    }
  },
  watch: {
    chartData: {
      deep: true,
      handler() {
        this.updateChart++
      }
    },
    change () {
      this.fillData()
    }
    
  },
  mounted () {
    this.fillData()
  },
  methods: {
    fillData() {
      this.datacollection = {
        
        labels: [this.$t('subheader_stat_hp'), this.$t('subheader_stat_atk'), this.$t('subheader_stat_def'), this.$t('subheader_stat_satk'), this.$t('subheader_stat_sdef'), this.$t('subheader_stat_speed')],
        datasets: [
          {
            backgroundColor: [
              'rgba(255, 138, 128, 0.8)',
              'rgba(255, 209, 128, 0.8)',
              'rgba(255, 255, 141, 0.8)',
              'rgba(130, 177, 255, 0.8)',
              'rgba(185, 246, 202, 0.8)',
              'rgba(255, 128, 171, 0.8)',
            ],
            borderColor: [
              'rgba(255, 138, 128, 1)',
              'rgba(255, 209, 128, 1)',
              'rgba(255, 255, 141, 1)',
              'rgba(130, 177, 255, 1)',
              'rgba(185, 246, 202, 1)',
              'rgba(255, 128, 171, 1)',
            ],
            borderWidth: 2,
            data: [this.$store.state.pkm_calc.base_hp, this.$store.state.pkm_calc.base_atk, this.$store.state.pkm_calc.base_def, 
                   this.$store.state.pkm_calc.base_satk, this.$store.state.pkm_calc.base_sdef, this.$store.state.pkm_calc.base_speed]
          },
          
        ],
        
      };
    },
  },
  
}
</script>

