<template>
  <div id="game">
    <v-hover
      disabled
    >
      <v-tooltip bottom>
        <template v-slot:activator="{on, attrs}">
          <v-card
            class="ma-2 pa-2"
            raised
            :elevation="hover ? 12 : 8"
            rounded
            color="grey lighten-5"
            v-bind="attrs"
            v-on="on"
            @mouseover="hover = true"
            @mouseleave="hover = false"
          >
            <v-card-titel>
              <heading_game />
            </v-card-titel>
            <v-layout row>
              <v-flex
                warp
                xs12
                md12
              >
                <select_game />
              </v-flex>
            </v-layout>
          </v-card>
        </template>
        <span> {{ tooltip }} </span>
      </v-tooltip>
    </v-hover>
  </div>
</template>

<script>

import heading_game from './game/heading_game.vue'
import select_game from './game/select_game.vue'

export default {
  name: 'Game',
  components: {
    heading_game,
    select_game
  },
  data() {
    return {
      hover: false,
      tooltip: this.$t('tooltip_game'),
    }
  }
}

</script>

