<template>
  <div id="InputLevel">
    <v-container
      id="input-usage"
      fluid
    >
      <span>
        &nbsp;&nbsp;{{ subheader }}
      </span>
      <v-text-field
        v-model.number="$store.state.pkm_calc.stat_atk"
        class=""
        type="number"
        outlined
        :placeholder="placeholder"
        solo
        dense
        flat
        background-color="orange accent-1"
        color="red darken-1"
        :label="placeholder"
        @click="$store.commit('calc');queryData()"
        @keyup="$store.commit('calc');queryData()"
        @mousedown="$store.commit('calc');queryData()"
      />
    </v-container>
  </div>
</template>

<script>
export default {
  name: 'InputLevel',
  data() {
    return {
      subheader: this.$t('subheader_stat_atk'),
      placeholder: this.$t('placeholder_stat_atk'),
    }
  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { a: this.$store.state.pkm_calc.stat_atk }) })
    }
  },
}
</script>

