<template>
  <div id="SelectStatHp">
    <v-container fluid>
      <span>
        <div class="ml-6 ml-md-3">
          {{ label }}
        </div>
      </span>
      <v-select
        v-model.number="$store.state.pkm_calc.judge_speed"
        class="mb-n3 mr-md-4 mx-4 mx-md-0"
        item-text="titel"
        item-value="key"
        :items="items"
        :label="label"
        :disabled="activ"
        solo
        hide-selected
        background-color="grey lighten-5"
        @change="$store.commit('calc');queryData()"
        @keyup="$store.commit('calc');queryData()"
        @click="$store.commit('calc');queryData()"
      />
    </v-container>
  </div>
</template>

<script>
export default {
  name: 'SelectStatSpeed',

  data() {
    return {
      activ: false,
      label  : this.$t('label_judge_speed'),
      items: [  
        { key: null, titel: ""},
        { key: 1, titel: this.$t('judge_1')},
        { key: 2, titel: this.$t('judge_2')},
        { key: 3, titel: this.$t('judge_3')},
        { key: 4, titel: this.$t('judge_4')},
        { key: 5, titel: this.$t('judge_5')},
        { key: 6, titel: this.$t('judge_6')},
        { key: 7, titel: this.$t('judge_7')} ]
    }
  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { js: this.$store.state.pkm_calc.judge_speed }) })
    }
  },
}
</script>

