<template>
  <div id="InputEvSpeed">
    <v-container
      id="input-usage"
      fluid
      class="mt-n8 mt-sm-0"
    >
      <span>
        &nbsp;&nbsp;{{ subheader }}
      </span>
      <v-text-field
        id=""
        v-model.number="$store.state.pkm_calc.ev_speed"
        class="mr-1"
        xs12
        type="number"
        :rules="rules"
        outlined
        :placeholder="placeholder"
        solo
        dense
        flat
        
        background-color="pink accent-1"
        color="red darken-1"
        :label="placeholder"
        @click="$store.commit('calc');queryData()"
        @keyup="$store.commit('calc');queryData()"
      />
    </v-container>
  </div>
</template>

<script>

export default {
  name: 'InputEvSpeed',
  data() {
    return {
      subheader: this.$t('subheader_stat_speed'),
      placeholder: this.$t('placeholder_ev_speed')
    }

  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { es: this.$store.state.pkm_calc.ev_speed }) })
    }
  },
}
</script>

