<template>
  <div id="StatSdef">
    <v-container
      id="input-usage"
      fluid
      class="mt-n8 mt-sm-0"
    >
      <span>
        &nbsp;&nbsp;{{ subheader }}
      </span>
      <v-text-field
        id=""
        v-model.number="$store.state.pkm_calc.stat_sdef"
        class=""
        type="number"
        outlined
        :placeholder="placeholder"
        solo
        dense
        flat
        background-color="green accent-1"
        color="red darken-1"
        :label="placeholder"
        @click="$store.commit('calc');queryData()"
        @keyup="$store.commit('calc');queryData()"
      />
    </v-container>
  </div>
</template>

<script>
export default {
  name: 'InputStatSdef',
  data() {
    return {
      subheader: this.$t('subheader_stat_sdef'),
      placeholder: this.$t('placeholder_stat_sdef'),
    }

  },
  methods: {
    queryData() {
      this.$router.push({ query: Object.assign({}, this.$route.query, { sd: this.$store.state.pkm_calc.stat_sdef }) })
    }
  },
}
</script>

