<template>
  <div id="ev">
    <v-hover
      disabled
    >
      <v-tooltip bottom>
        <template v-slot:activator="{on, attrs}">
          <v-card
            class="ma-2 pa-1"
            raised
            :elevation="hover ? 12 : 8"
            :class="{'on-hover': hover}"
            rounded
            color="grey lighten-5"
            v-bind="attrs"
            v-on="on"
            @mouseover="hover = true"
            @mouseleave="hover = false"
          >
            <v-card-titel>
              <HeaderEv />
            </v-card-titel>
            <v-layout row>
              <v-flex
                warp
                xs12
                md12
              >
                <InputEv />
              </v-flex>
            </v-layout>
          </v-card>
        </template>
        <span> {{ tooltip }} </span>
      </v-tooltip>
    </v-hover>
  </div>
</template>

<script>

import HeaderEv from './ev/HeaderEv.vue'
import InputEv from './ev/InputEv.vue'

export default {
  name: 'Ev',
  components: {
    HeaderEv,
    InputEv
  },
  data() {
    return {
      hover: false,
      tooltip: this.$t('tooltip_ev'),
    }
  }

}

</script>

